
.crdit-card{
    transition: all 150ms ease-out;
    background-color: #2b580c;
    height: 20em;
    width: 45em;
    margin-top: 2em;
    border-radius: 2em;
    box-shadow: 0px 4px 16px 1px black;
    position: relative;
    
    .background{
        background-color: white;
        opacity: 30%;
        width: 46em;
        height: 20em;
        margin-top: -20em;
        margin-left: -1em;
        border-radius: 2em;
    }
    

    .submit{
        margin-right: 12em;
        margin-bottom:38px;

        .button{
            background-color: #649d66;
            border: none;
            border-radius: 6px;
            font-family: 'Titillium Web', sans-serif;
        }
    }

    .icon{
        transition: all 150ms ease-out;
        opacity: 0%;
    }

    .digits{
        margin-bottom:30px;
        margin-left: 30px;
        font-size: 20px;
        color: red;
        font-weight: bold;

        #secret-code{
            letter-spacing: 42px;
            color: #d9bf77;
            background-color: transparent;
            margin: 8px 0;
            box-sizing: border-box;
            border: none;
            border-bottom: 2px solid #d9bf77;
        }
    }
}


