@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;display:flex;justify-content:center;align-items:center;font-family:"Open Sans", sans-serif}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.login{width:27em;height:40em;display:flex;justify-content:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:flex;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:17px 10px}.login .right-side{display:flex;flex-direction:column;justify-content:center;height:90%;background-color:#3498db;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#3498db;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2386c8;color:#a6a6a6}.btn:focus{outline:none}.Register{width:27em;height:40em;justify-content:center;align-items:center;margin-top:5px;margin-bottom:5px;position:relative;z-index:99}.Register .container{margin-left:22em;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:130%;z-index:99;padding:17px 10px}

.base-container{width:100%;display:flex;flex-direction:column;align-items:center}.base-container .header{font-size:24px;font-family:"Open Sans" , sans-serif}.base-container .content{display:flex;flex-direction:column}.base-container .content .image{width:21em}.base-container .content .image img{width:100%;height:100%}.base-container .content .form{margin-top:2em;display:flex;flex-direction:column;align-items:center}.base-container .content .form .form-group{display:fixed;flex-direction:column;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .content .form .form-group label{font-size:24px;font-family:"Open Sans" , sans-serif}.base-container .content .form .form-group input{margin-top:10px;min-width:18em;height:37px;padding:0px, 10px;font-size:16px;font-family:"Open Sans" , sans-serif;background-color:#f3f3f3;border:0;border-radius:4px;margin-bottom:3px;transition:all 250ms ease-in-out}.base-container .content .form .form-group input:focus{outline:none}.base-container .content .footer{margin-top:3em}

.crdit-card{transition:all 150ms ease-out;background-color:#2b580c;height:20em;width:45em;margin-top:2em;border-radius:2em;box-shadow:0px 4px 16px 1px black;position:relative}.crdit-card .background{background-color:white;opacity:30%;width:46em;height:20em;margin-top:-20em;margin-left:-1em;border-radius:2em}.crdit-card .submit{margin-right:12em;margin-bottom:38px}.crdit-card .submit .button{background-color:#649d66;border:none;border-radius:6px;font-family:'Titillium Web', sans-serif}.crdit-card .icon{transition:all 150ms ease-out;opacity:0%}.crdit-card .digits{margin-bottom:30px;margin-left:30px;font-size:20px;color:red;font-weight:bold}.crdit-card .digits #secret-code{letter-spacing:42px;color:#d9bf77;background-color:transparent;margin:8px 0;box-sizing:border-box;border:none;border-bottom:2px solid #d9bf77}

